nav.top-nav {
	position: fixed;
	top: 0;
	z-index: 121;
	background-color: #fff;
	color: $color-main-dark;
	font-family: $font-main;
	font-weight: 400;
	padding: 0.6em;
	width: 100%;
	.container {
		display: flex;
		position: relative; }
	img {
		height: 40px;
		float: left;
		&.nav-chevron {
			display: none;
			filter: invert(1);
			transform: rotate(0);
			transition: transform .2s;
			&.active {
				transform: rotate(180deg); } }
		&.emoji {
			float: none; } }
	.nav-links {
		position: relative;
		right: 0;
		flex: 1;
		text-align: right;
		padding-top: 0.5em;
		a {
			color: $color-main-dark;
			text-decoration: none;
			margin-left: 1em; }
		span.normal-links {
			margin-right: 1em; }
		span.bold-links {
			font-weight: 600;
			border-left: 1px solid $color-main-dark; } }
	a#lang-switch {
		cursor: pointer; }
	.lang-dropdown {
		position: absolute;
		top: 48px;
		right: 0;
		background: #fff;
		border-radius: 0 0 5px 5px;
		box-shadow: 0 6px 15px rgba(0,0,0,0.12);
		font-weight: bold;
		visibility: hidden;
		opacity: 0;
		transition: opacity .2s;
		&.show {
			visibility: visible;
			opacity: 1; }
		ul {
			a {
				display: block;
				padding: 0.5em 1em;
				color: #000;
				text-decoration: none;
				&:hover {
					background: $color-main;
					color: #fff; }
				img {
					height: 10px;
					float: none; } }
			li:not(:last-child) {
				border-bottom: 1px solid #dbdfe1; }
			li:last-child {
				a {
					border-radius: 0 0 5px 5px; } } } } }

.mobile-nav {
	position: fixed;
	z-index: 120;
	padding: 4em 1em 1em;
	font-family: $font-main;
	background-color: rgba(45, 86, 158, 0.93);
	backdrop-filter: blur(10px);
	width: 100%;
	height: 100%;
	display: none;
	overflow-y: auto;
	ul {
		text-align: center;
		li {
			border-bottom: 1px solid rgb(45, 86, 158)+65;
			color: #fff;
			transition: all .2s;
			&:hover:not(.nohover) {
				font-weight: bold;
				color: $color-main;
				background-color: #fff;
				a {
					color: $color-main; } }
			a {
				display: block;
				padding: 1em 0;
				text-decoration: none;
				color: #fff; } }
		&:last-child {
			li:last-child {
				border-bottom: none;
				margin-bottom: 0.5em; } }
		ul:not(.unstyled) {
			margin-left: 1.3em;
			margin-right: 1.3em; } } }
.top-nav-secondary {
	position: fixed;
	top: 59px;
	z-index: 119;
	width: 100%;
	padding: 0;
	margin: 0 auto;
	text-align: center;
	font-family: $font-main;
	background-color: rgb(242,242,242);
	border-bottom: 1px solid #bcc0c2;
	ul {
		li {
			&.current-page-ancestor {
				box-shadow: inset 0 -2px 0 $color-main;
				color: #000; } } } }

.content-nav {
	position: static;
	margin-bottom: 2em;
	width: 100%;
	text-align: center;
	font-family: $font-main;
	background-color: rgb(242,242,242);
	border-bottom: 1px solid #bcc0c2; }

.content-nav, .top-nav-secondary {
	ul {
		overflow: auto;
		white-space: nowrap;
		list-style-type: none;
		margin: 0;
		li {
			display: inline-block;
			padding: 0.6em 0;
			margin-right: 3em;
			&:hover, &.active, &.current-menu-item {
				box-shadow: inset 0 -3px 0 $color-main;
				color: #000; }
			&:last-child {
				margin-right: 0; }
			a {
				color: #000;
				text-decoration: none; } } } }

@media screen and (max-width: 1023px) {
	.content-nav, .top-nav-secondary {
		ul {
			li:first-child {
				margin-left: 1em; }
			li:last-child {
				margin-right: 1em; }
			li:not(:last-child) {
				margin-right: 1em; } } } }

@media screen and (max-width: 768px) {
	nav.top-nav {
		padding-left: 1em;
		padding-right: 1em;
		.nav-links {
			display: none; }
		.logo {
			flex: 1; }
		a {
			float: none; }
		img.nav-chevron {
			display: inline-block;
			height: 28px;
			margin-top: 6px; }
		.lang-dropdown {
			visibility: hidden;
			&.show {
				visibility: hidden; } } } }

@media screen and (max-width: 604px) {
	nav.top-nav {
		box-shadow: 0 8px 12px rgba(0,0,0,0.05); }
	.top-nav-secondary {
		display: none; } }
