/* Courses page */

section.page--courses {
	padding-left: 0;
	padding-right: 0;
	padding-bottom: 0;
	.page-content {
		padding-bottom: 0; }
	.page-hero {
		background-image: url('./img/course-hero.jpg');
		h3 {
			max-width: 493px;
			margin-left: auto;
			margin-right: auto; } } }

section.all-courses {
	background-color: #f3f3f3;
	div[class*="col-"] {
		margin-bottom: 1.2em; }
	div.card {
		display: flex;
		flex-direction: column;
		// &.master
		// 	border-color: #bde6ff
		// &.phd
		// 	border-color: #78bdff
		.info {
			flex: 1;
			display: flex;
			flex-direction: column; }
		.button {
			margin-top: auto;
			margin-bottom: 0.5em; } }
	div.arrow-wrap {
		margin-bottom: 1.2em;
		p {
			margin-bottom: 0.3em;
			&:last-of-type {
				margin-bottom: 0; } } } }
